@mixin box-shadow($shadow...) {
  -webkit-box-shadow: $shadow;
          box-shadow: $shadow;
}

@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity-ie: ($opacity * 100);
  filter: alpha(opacity=$opacity-ie);
}

@mixin transition($transition...) {
  -webkit-transition: $transition;
       -o-transition: $transition;
          transition: $transition;
}

@mixin button-variant($color, $background, $border) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &.button--default:focus,
  &.button--default.focus {
    color: $button-default-color;
    background-color: $button-default-bg-focus;
        border-color: $button-default-bg-focus;
  }
  &.button--default:hover {
    color: $button-default-color;
    background-color: $button-default-bg-hover;
        border-color: $button-default-bg-hover;
  }

  &.button--primary:focus,
  &.button--primary.focus {
    color: $button-primary-color;
    background-color: $button-primary-bg-focus;
        border-color: $button-primary-bg-focus;
  }
  &.button--primary:hover {
    color: $button-primary-color;
    background-color: $button-primary-bg-hover;
        border-color: $button-primary-bg-hover;
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $color;
    background-color: darken($background, 10%);
        border-color: darken($border, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: $color;
      background-color: darken($background, 17%);
          border-color: darken($border, 25%);
    }
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus,
    &.focus {
      background-color: $background;
          border-color: $border;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}
