@import "../../styles/variables";


.search-page {
  padding: calc(#{$padding-vertical} * 4) calc(#{$padding-horizontal} * 4);

  .search-page__header {
    display: flex;
    flex-direction: column;
    padding-bottom: calc(#{$padding-vertical} * 2);

    .search-page__header__title {
      padding: calc(#{$padding-horizontal} * 2) 0;

      * {
          font-size: calc(#{$font-size} * 2);
      }
    }
    .search-page__header__actions {
      //
    }
  }

  .search-page__body {
    //

    .search-page__body__total-hits {
      font-size: calc(#{$font-size} / 2);
      padding: $padding-horizontal 0;
    }

    .search-page__body__loading {
      padding: calc(#{$padding-vertical} * 4) calc(#{$padding-horizontal} * 4);
      
      * {
          font-size: 2rem;
          color: $color-gray;
          font-weight: bold;
          text-align: center;
      }
    }
  }
}