@import "../../styles/variables";
@import "../../styles/mixins";


.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  .popup__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: $color-black;
    opacity: 0.5;
  }

  .popup__container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: scroll;

    .popup__body {
      position: absolute;
      display: block;
      width: 40rem;
      background-color: #FFFFFF;
      margin: calc(#{$padding-vertical} * 8) calc((100% - 40rem) / 2);
      padding: calc(#{$padding-vertical} * 4) calc(#{$padding-horizontal} * 2);
      border-radius: $border-radius;
      @include box-shadow(0 0 1.5rem $color-gray-darken);

      .popup__body__close {
        position: absolute;
        right: calc(#{$padding-horizontal} * 2);
        cursor: pointer;
      }
    }
  }
}