@import "../../styles/variables";


.collection-page {
  padding: calc(#{$padding-vertical} * 4) calc(#{$padding-horizontal} * 4);

  .collection-page__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: calc(#{$padding-vertical} * 6);
  
    .collection-page__header__title {
      * {
          font-size: 3.5rem;
          color: $color-gray;
          font-weight: bold;
      }
    }
    .collection-page__header__actions {
      //
    }
  }

  .collection-page__body {
    //

    .collection-page__body__nothing {
      padding: calc(#{$padding-vertical} * 8) calc(#{$padding-horizontal} * 8);
      
      * {
          font-size: 2rem;
          color: $color-gray;
          font-weight: bold;
          text-align: center;
      }
    }
  }

}