@import "../../../../styles/variables";


.thumbnail {
    background: url('') center center no-repeat;
    background-size: cover;
    border-radius: $border-radius;
    width: 100%;
    height: 12rem;
    margin-bottom: calc(#{$margin-vertical} * 4);
    box-shadow: 0 1.5rem 3rem -1rem $color-gray-darken;
    display: flex;
    justify-content: center;
    align-items: center;

    .thumbnail__play {
      padding: 1rem 0.8rem 1rem 1.2rem;
      background: $color-white;
      color: $color-black;
      border-radius: 50%;
      width: 2rem;
      height: 2rem;
      line-height: 2rem;
      text-align: center;
    }
  
    @media (max-width: 60rem) {
      & {
        height: 10rem;
      }
    }
    @media (max-width: 30rem) {
      & {
        height: 8rem;
      }
    }
  }