body {
  font-size: $font-size;
  line-height: $line-height;
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-size: $font-size;
}

a {
  color: $color-purple;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

// Text
// -----------------------------------

.text--xsmall {
  font-size: calc(#{$font-size} / 2.25);
}
.text--small {
  font-size: calc(#{$font-size} / 1.5);
}
.text--medium {
  font-size: $font-size;
}
.text--large {
  font-size: calc(#{$font-size} * 1.5);
}
.text--xlarge {
  font-size: calc(#{$font-size} * 2.25);
}

.text--justify {
  text-align: justify;
}

.text--hidden--ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Color
// -----------------------------------

.color--white {
  color: $color-white;
}

.color--gray {
  color: $color-gray;
}
.color--dark-gray {
  color: $color-gray-darken;
}
.color--light-gray {
  color: $color-gray-lighten;
}

// Skeleton Loading
// -----------------------------------

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0
  }
  100% {
    background-position: 468px 0
  }
}

.skeleton-loading {
  position: relative;
  border-color: #eee !important;
  color: #eee !important;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    background: #f6f7f8;
    background: linear-gradient(to right, #eeeeee 8%, #e6e6e6 18%, #eeeeee 33%);
    background-size: 800px 104px;
    border-color: #eee !important;
    color: #eee !important;
    -webkit-border-radius: 4px !important;
    -moz-border-radius: 4px !important;
    border-radius: 4px !important;
    -webkit-appearance: none !important;
    -moz-appearance: none !important;
    appearance: none !important;
    cursor: not-allowed !important;
  }

  & * {
    color: #eee !important;
    border-color: #eee !important;
    cursor: not-allowed !important;
  }

  &::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #eee !important;
  }
  
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: #eee !important;
  }
  
  &:-ms-input-placeholder {
    /* IE 10+ */
    color: #eee !important;
  }
  
  &:-moz-placeholder {
    /* Firefox 18- */
    color: #eee !important;
  }
}

// Input
// -----------------------------------

.form-control {
  display: block;
  width: calc(100% - (#{$padding-horizontal} * 2));
  padding: $padding-vertical $padding-horizontal;
  margin-bottom: calc(#{$margin-vertical} * 4);
  font-size: $font-size;
  line-height: $line-height;
  color: $input-color;
  background-color: $input-bg;
  background-image: none;
  border: 1px solid $input-border;
  border-radius: $input-border-radius;
  @include box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
  @include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);

  &:hover,
  &:focus {
    @include box-shadow(0 0 .5rem $color-gray);
    border-radius:$color-gray-darken;
    outline: none;
  }

  &::placeholder {
    color: $color-gray-lighten;
    opacity: 1;
  }

  &:-ms-input-placeholder {
    color: $color-gray-lighten;
  }

  &::-ms-input-placeholder {
    color: $color-gray-lighten;
  }

  &.form-control--small {
    width: calc(100% - ((#{$padding-horizontal} / 1.5) * 2));
    height: calc(#{$input-height} / 1.5);
    padding: calc(#{$padding-vertical} / 1.5) calc(#{$padding-horizontal} / 1.5);
    font-size: calc(#{$font-size} / 1.5);
  }
}


.input {
  height: $input-height;

  &.form-control--small {
    height: calc(#{$input-height} / 1.5);
  }
}


.textarea {
  height: auto;

  &.form-control--small {
    height: auto;
  }
}

.select {
  width: 100%;

  &.form-control--small {
    width: 100%;
  }
}
