$color-white: #FFFFFF;
$color-black: #000000;

$color-gray: #B2B2B2;
$color-gray-lighten: #CCCCCC;
$color-gray-darken: #555555;

$color-purple: #8F00BE;
$color-purple-lighten: #AE00E0;
$color-purple-darken: #710097;

$font-size: 2rem;
$line-height: 1.4;
$line-height-computed: floor(($font-size * $line-height));
$border-radius: 4px;

$input-bg: $color-white;
$input-bg-disabled: $color-gray-lighten;
$input-color: $color-gray-darken;
$input-border: $color-gray-lighten;
$input-border-radius: $border-radius;

$padding-vertical: .5rem;
$padding-horizontal: 1rem;

$margin-vertical: .5rem;
$margin-horizontal: 1rem;

$button-font-weight: normal;

$button-default-color: $color-gray-darken;
$button-default-bg: $color-white;
$button-default-bg-hover: $color-gray-lighten;
$button-default-bg-focus: $color-white;
$button-default-border: $color-gray-lighten;

$button-primary-color: $color-white;
$button-primary-bg: $color-purple;
$button-primary-bg-hover: $color-purple-lighten;
$button-primary-bg-focus: $color-purple-darken;
$button-primary-border: darken($button-primary-bg, 5%);

$link-color: $color-purple;
$link-hover-color: $color-purple-lighten;
$link-hover-decoration: underline;
$button-link-disabled-color: $color-gray-lighten;

$input-height: ($line-height-computed + ($padding-vertical * 2) + 2);