@import "../../styles/variables";


.video-page {
  padding: calc(#{$padding-vertical} * 4) calc(#{$padding-horizontal} * 4);
  background-color: #333333;

  .video-page__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: $padding-vertical;

    * {
      font-size: 4rem;
      color: $color-gray-lighten;
    }

    .video-page__header__title {
      //
    }
    .video-page__header__actions {
      * {
        cursor: pointer;
      }
    }
  }

  .video-page__body {
    box-shadow: 0 1.5rem 3rem -1rem $color-gray-darken;
  }
}