@import "../../styles/variables";


.collection {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  margin: -2rem -1rem;

  .collection__item {
    padding: 2rem 1rem;
    display: flex;
    width: calc((100% / 3) - 2rem);
    overflow: hidden;

    @media (max-width: 60rem) {
      & {
        width: calc((100% / 2) - 2rem);
      }
    }
    @media (max-width: 30rem) {
      & {
        width: calc(100% - 2rem);
      }
    }
  }
}